@import "~animate.css/animate.css";

* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;  
}

input {
  color:grey;
  width: 100%;
  font-size: 1.3rem;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;  
}

.img-container {
  text-align: center;
}